<template>
    <div @mouseleave='show_menu=false' class="dropdown">
        <input 
            @keyup="show_menu=true"
            @focus="show_menu=true"
            @click="show_menu=true"
            v-model="keyword"
            :placeholder="placeholder"
            type="search"
            class="search">
        <div v-if="show_menu && result.length" class="menu">
            <div 
                @click="select(row)" 
                v-for="row in result" 
                :key="row[displayKey]" 
                class="menu-item">{{row[displayKey]}}
            </div>
        </div>
    </div>
</template>

<script>
import api from "../lib/api";

export default {
  props: {
    api: {}, // {string|object} 
    list: {
      // {Array} [{name: whh, id: 1}, ...]
      default() {
        return [];
      }
    },
    default: {}, // {mixed} id
    onSelect: {}, // {Function} 
    primaryKey: {
      // {string} 
      default: "id"
    },
    displayKey: {
      // {string} name
      default: "name"
    },
    placeholder: {
      default: ""
    }
  },

  data: function() {
    return {
      api_conf: {}, // props.api
      result: [], // 
      keyword: "", // 
      selected: {}, // 
      show_menu: false, // 
      timer: null // debounce
    };
  },

  mounted() {
    this.set_default(); // props.default, 
    this.api_conf = this.parse_api(); // props.api
    let list = this.list;
    list && (this.result = Object.assign([], this.list)); // 
  },

  methods: {
    /**
     * 
     */
    set_default() {
      let key = this.default;

      if (!key) {
        this.reset();
        return;
      }

      let def = this.list.find(row => {
        return row[this.primaryKey] == key;
      });
      this.select(def);
    },

    /**
     * 
     * @param {object} row
     */
    select(row) {
      this.selected = row;
      this.show_menu = false;
      this.keyword = row[this.displayKey];

      if (this.onSelect) {
        this.onSelect(row);
      }
    },

    /**
     * 
     * api
     */
    search() {
      let condition = {};
      let property = this.api_conf.property;

      if (!property) {
        return;
      }

      this.api_conf.property.forEach(prop => {
        condition[prop] = this.keyword;
      });

      clearTimeout(this.timer);

      this.timer = setTimeout(() => {
        api(`${this.api_conf.model}/search`, { or: condition }).then(r => {
          this.result = r.data.data;
        });
      }, 300);
    },

    /**
     * 
     * search
     */
    filter() {
      this.result = Object.assign([], this.list);
      this.result = this.result.filter(row => {
        return row[this.displayKey].indexOf(this.keyword) != -1;
      });
    },

    /**
     * 
     */
    reset() {
      this.keyword = "";
      this.selected = {};
    },

    /**
     * props.api
     * @returns {object}
     */
    parse_api() {
      let api_prop = this.api;
      if (typeof api_prop != "string") {
        return Object.assign({}, api_prop);
      }

      api_prop = api_prop.split(".");
      let model = api_prop[0];
      let property = api_prop[1];

      property = property.split(",");

      return {
        model,
        property
      };
    }
  },

  watch: {
    /**
     * 
     */
    default() {
      this.set_default();
    },

    /**
     * 
     */
    keyword() {
      if (this.api) {
        this.search();
      } else {
        this.filter();
      }
    },

    list: {
      deep: true,
      handler(n) {
        this.result = n;
      }
    },
  }
};
</script>

<style scoped>
.dropdown {
  position: relative;
  display: inline-block;
  background: #fff;
}

.search {
  width: 100px;
}

.selected,
.menu > * {
  display: block;
  padding: 5px 10px;
}

.selected,
.menu {
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.menu {
  background: #fff;
  z-index: 1;
  width: 100%;
  position: absolute;
  left: 0;
}

.selected,
.menu-item {
  cursor: pointer;
}

.menu-item:hover {
  background: rgba(0, 0, 0, 0.1);
}
</style>
