<script>
import api from "../lib/api";

import validator from "../directive/validator.js";

import Nav from "../components/Nav";
import AdminNav from "../components/AdminNav";
import Pagination from "../components/Pagination";
import Dropdown from '../components/Dropdown';

export default {
  directives: { validator },

  components: {
    Nav,
    AdminNav,
    Pagination,
    Dropdown,
  },

  data() {
    return {
      // 
      model: "", // 'user', 'vehicle'

      // 
      keyword: "", // 
      searchable: [], // 

      // 
      edit_pattern: false, // 

      // 
      current: {}, // 
      list: [], // 

      // 
      total: 0, // 
      last_page: 0, // 
      current_page: 1, // 
      limit: 5 // 
    };
  },

  methods: {
    /**
     * 
     * @param {number} 
     */
    on_page_change(page) {
      this.read(page);
    },

    /**
     * 
     * create or update
     *
     * id
     */
    cou() {      
      let action = this.current.id ? "update" : "create";
      api(`${this.model}/${action}`, this.current).then(r => {
        if (r.data.success) {
          this.read();
          this.current = {};
          this.edit_pattern = false;
        }
      });
    },

    /**
     * id
     * @param {number} id
     */
    remove(id) {
      if (!confirm("")) {
        return;
      }
      api(`${this.model}/delete`, { id: id }).then(r => {
        this.read();
      });
    },

    /**
     * this.list
     * @param {number} 
     */
    read(page = 1) {
      if (page == this.current_page && page != 1) {
        return;
      }

      api(`${this.model}/read`, {
        page: page,
        limit: this.limit,
        with: this.with
      }).then(r => {
        this.list = r.data.data;
        this.current_page = r.data.current_page;
        this.last_page = r.data.last_page;
        this.total = r.data.total;
      });
    },

    /**
     * 
     */
    search() {
      let param = {
        or: {}
      };

      this.searchable.forEach(prop => {
        param.or[prop] = this.keyword;
      });

      api(`${this.model}/search`, param).then(r => {
        this.list = r.data.data;
        this.keyword = "";
      });
    },

    /**
     * 
     */
    cancle() {

      this.edit_pattern = false;
      this.current = {};
    },

    set_current(row) {
      this.current = row;
      this.edit_pattern = true;

      if (this.after_set_current) {
          this.after_set_current();
      }
    },

    is_update() {
        return !!this.current.id;
    }
  },

  mounted() {
    if (!this.model) {
      throw new Error("datamodel");
    }

    // , 
    this.read();
  }
};
</script>   