import Vue from 'vue';
import api from '../lib/api';

/**
 * 
 * @param {string} str 
 * @returns {Object} 
 */
function parse_string_rule(str) {
  let rule = {};

  str
    .split('|')
    .forEach(r => {
      let arr = r.split(':');
      let key = arr[0]; // 'min_length:4'keymin_length
      let val = arr[1]; // 'min_length:4'val4
      if (val)
        val = val.split(',');

      // 'required'valtrue
      val = val === undefined ? true : val;

      rule[key] = val;
    });

  return rule;
}

// 
const valid = {
  /**
   * 
   * @param val 
   * @param lang 
   */
  mail(val, lang) {
    const lang_conf = {
      zh: '',
      en: 'Invalid email',
    };

    let re = /[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[a-zA-Z]/;

    if (!re.test(val)) {
      throw lang_conf[lang];
    }

    return true;
  },

  shadow(val, lang, selector) {
    const lang_conf = {
      zh: '',
      en: 'Inconsistent double inputs',
    };

    let reference = document.querySelector(selector);
    let value = reference.value;

    if (val != value) {
      throw lang_conf[lang];
    }

    return true;
  },

  /**
   * 
   * @param val
   * @param lang
   */
  numeric(val, lang) {
    const lang_conf = {
      zh: '',
      en: 'Only number',
    };

    let r = parseFloat(val) == val;

    if (!r)
      throw lang_conf[lang];

    return r;
  },

  /**
   * 
   * @param val
   * @param lang
   */
  positive(val, lang) {
    const lang_conf = {
      zh: '',
      en: 'Invalid number',
    };

    let r = this.numeric(val) && parseFloat(val) > 0;

    if (!r)
      throw lang_conf[lang];

    return r;
  },

  /**
   * 
   * @param val
   * @param lang
   * @param max
   */
  max(val, lang, max) {
    const lang_conf = {
      zh: '' + max,
      en: 'Max value: ' + max,
    };

    let r = this.numeric(val) && parseFloat(val) <= max;

    if (!r)
      throw lang_conf[lang];

    return r;
  },

  /**
   * 
   * @param val
   * @param lang
   * @param min
   */
  min(val, lang, min) {
    const lang_conf = {
      zh: '' + min,
      en: 'Min value: ' + min,
    };

    let r = this.numeric(val) && parseFloat(val) >= min;

    if (!r)
      throw lang_conf[lang];

    return r;
  },

  /**
   * 
   * @param val
   * @return {boolean}
   */
  username(val, lang) {
    const lang_conf = {
      zh: '',
      en: 'Invalid username',
    };

    const re = /^[a-zA-Z0-9]*$/;
    let r = re.test(val);

    if (!r)
      throw lang_conf[lang];

    return r;
  },

  /**
   * 
   * @param val 
   * @param lang 'zh'
   * @param model 
   * @param property 
   * @param except 
   */
  not_exist(val, lang, model, property, except) {
    return new Promise((s, j) => {
      if (!val || val == except)
        s();

      return api(`${model}/first`, {
          where: {
            and: {
              [property]: val
            }
          }
        })
        .then(r => {
          return r.data.data ? j('') : s(true);
        });
    });
  },

  /**
   * 
   * @param val
   * @return {boolean}
   */
  required(val, lang) {
    const lang_conf = {
      zh: '',
      en: 'This field is required',
    };

    if (typeof val === 'number')
      return true;

    let r = !!val;

    if (!r)
      throw lang_conf[lang];

    return r;
  },

  /**
   * 
   * @param val
   * @param min
   * @return {boolean}
   */
  min_length(val, lang, min) {
    const lang_conf = {
      zh: '' + min,
      en: 'Minimal length: ' + min,
    };

    let r = val.length >= parseInt(min);

    if (!r)
      throw lang_conf[lang];

    return r;
  },

  /**
   * 
   * @param val
   * @param max
   * @return {boolean}
   */
  max_length(val, lang, max) {
    const lang_conf = {
      zh: '' + max,
      en: 'Maximum length: ' + max,
    };

    let r = val.length <= parseInt(max);

    if (!r)
      throw lang_conf[lang];

    return r;
  },

  /**
   * 
   */
  cellphone(val, lang) {
    const lang_conf = {
      zh: '',
      en: 'Invalid phone number',
    };

    if (!this.numeric(val, lang) || !this.length(val, lang, 11)) {
      throw lang_conf[lang];
    }

    return true;
  },

  /**
   * 
   */
  length(val, lang, len) {
    const lang_conf = {
      zh: '' + len + '',
      en: 'Invalid field length, length should equlas to ' + len,
    };

    val = val.toString();

    if (val.length == len) {
      return true;
    }

    throw lang_conf[lang];
  },
};

/**
 * form_valid
 * @param valid
 */
// function set_form_valid (valid) {
//   form_valid = valid;
// }

/**
 * inputinvalid
 * @param input_list inputel_form.$state.input_list
 * @param el_submit 
 */
function validate_form(input_list, el_submit) {
  let invalid = false;

  input_list.forEach(input => {
    if (input.getAttribute('invalid') === 'true') {
      invalid = true;
    }
  });

  if (invalid)
    disable_submit(el_submit);
  else
    enable_submit(el_submit);
}

/**
 * 
 */
function disable_submit(el_submit) {
  el_submit.setAttribute('disabled', 'disabled');
}

/**
 * 
 */
function enable_submit(el_submit) {
  el_submit.removeAttribute('disabled');
}

/**
 * 
 * @param {string} form 
 * @param {string} lang 
 */
function init_form_state(form, lang) {
  let el_submit = form.querySelector('[type=submit]');

  if (!el_submit) {
    throw 'Missing submit button';
  }

  form.$state = {
    lang: lang,
    el_submit: el_submit,
    input_list: [],
  };
  // form.$state.input_list = form.$state.input_list || [];
}

function track_input(form, input) {
  form.$state.input_list.push(input);
}

/**
 * 
 * @param el_form input
 * @param el_input input
 * @param el_error 
 * @param rule  {required: true, min_length:4}
 */
function go(el_form, el_input, el_error, rule) {
  let val = el_input.value;
  let invalid = false;
  let lang = el_form.$state.lang;

  // 
  // 
  // <div class="error">1...</div>
  // <div class="error">2...</div>
  let inner_msg = '';
  set_invalid(false);

  if (!val && !rule.required)
    return;

  if (rule.required) {
    try {
      valid.required(val, lang);
    } catch (e) {
      set_invalid(true, e);
      return;
    }
  }

  // 
  for (let type in rule) {
    // type'required''username'
    let arg = rule[type]; // 'min_length:4''4'

    let validator = valid[type].bind(valid); // 
    try {
      let args = [val, el_form.$state.lang].concat(arg);

      if (!invalid) {
        let result = validator(...args);
        if (result instanceof Promise) {
          result
            .then(r => {
              if (r)
                set_invalid(false);
            })
            .catch((r) => {
              set_invalid(true, r);
            });
        }
      }
    } catch (e) {
      set_invalid(true, e);
      break;
    }
  }

  function set_invalid(invalid, e) {
    if (invalid) {
      inner_msg += `<div class="error">${e}</div>`;
      el_input.setAttribute('invalid', 'true');
    } else
      el_input.setAttribute('invalid', 'false');

    if (el_input.getAttribute('dirty') === 'true') {
      el_error.innerHTML = inner_msg;
    }

    validate_form(el_form.$state.input_list, el_form.$state.el_submit);
  }
}


export default Vue.directive('validator', {
  /**
   * DOM
   * @param el
   * @param binding
   */
  inserted: function (el, binding) {
    let debounce_timer;
    // 
    let rule = binding.value; // 'required|username|min_length:4'
    let selector = el.getAttribute('error-el'); // 
    let error_el = document.querySelector(selector); // 

    if (!error_el) {
      error_el = document.createElement('div');
      error_el.classList.add('error-list');
      el.insertAdjacentElement('afterEnd', error_el);
    }

    // 
    if (typeof rule === 'string')
      rule = parse_string_rule(binding.value); // {required: true, min_length: 4}

    // @var {HTMLFormElement} 
    let el_form = el.closest('form');

    // input<input><form>
    if (!el_form.$state) {
      let lang = el.getAttribute('error-lang') || 'zh'; // 
      init_form_state(el_form, lang);
    }

    // input$state
    track_input(el_form, el);

    // 
    go(el_form, el, error_el, rule);

    function on_input_change() {
      clearTimeout(debounce_timer);
      debounce_timer = setTimeout(() => {
        go(el_form, el, error_el, rule);
      }, 300);
    }

    // 
    el.addEventListener('keyup', on_input_change);
    el.addEventListener('change', on_input_change);

    el.addEventListener('keyup', () => {
      el.setAttribute('dirty', 'true');
    });
  },
});